@font-face{
  font-family: 'MontserratLight';
  src: url(./fonts/Montserrat-ExtraLight.ttf);
}
@font-face{
  font-family: 'MontserratMedium';
  src: url(./fonts/Montserrat-Medium.ttf);
}
@font-face{
  font-family: 'MontserratBold';
  src: url(./fonts/Montserrat-Bold.ttf);
}
@font-face{
  font-family: 'Merriweather';
  src: url(./fonts/Merriweather-Light.ttf);
}

html{
  font-size: 16px;
}

body{
  margin: 0;
  padding: 0;
  font-family: MontserratMedium, Arial, Helvetica, sans-serif;
}

#background{
  position: fixed;
  top: 0;
  left: 0;
  min-height: 100vh;
  width: 100%;
  background: url('./images/alps.jpeg');
  background-size: cover;
  z-index: 1;
}

#wrapper{
  position: sticky;
  top: -175px;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  overflow: hidden;
}

#headingContainer{
  color: white;
  z-index: +100;
  width: 100%;
  position: relative;
  top: 175px;
  font-family: MontserratLight;
  text-align: center;
  transition: opacity ease 0.5s;
  z-index: 2;
}

#mainHeading{
  font-size: 3rem;
  line-height: 1.35em;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

#tagLine{
  font-size: 1.5rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.fa-angles-down {
  font-size: 30px;
}

#contentContainer{
  position: relative;
  left: 50%;
  width: 1200px;
  transform: translateX(-50%);
  background: white;
  color: rgb(33, 41, 49);
  z-index: 2;
  margin-bottom: 3.75rem;
}

#topSection{
  width: 100%;
  height: 3.75rem;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
  background-color: rgba(255, 255, 255, 0.2);
}

#intro{
  padding: 6.25rem;
}

#content{
  transition: opacity ease 0.8s;
}

#skills, #experience{
  padding: 3.75rem 3.75rem 6.25rem;
  border-top: solid 2px rgba(33, 41, 49, 0.3);
}

#skills h1, #experience h1{
  margin: 0;
  text-align: center;
  background: white;
  padding-top: 30px;
  padding-bottom: 30px;
  position: sticky;
  top: 0;
  z-index: 2;
}

.skillList{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.skill{
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill .img-container{
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.skill img{
  width: 50px;
}

#intro p, #skills p {
  font-size: 1.25rem;
  text-align: center;
}

#skills h2 {
  margin-top: 3rem;
  margin-bottom: 3rem;
  text-align: center
}

.experience {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 3.75rem 0px;
}

.experience:nth-child(odd){
  flex-direction: row-reverse;
}

.experience-image-container{
  width: 45%;
}

.experience-image-container img{
  width: 100%;
}

.experience-description{
  display: flex;
  flex-direction: column;
  width: 45%;
  text-align: left;
  align-items: flex-start;
  justify-content: center;
}

.experience-description .dates{
  margin: 0;
  text-align: left;
  opacity: 0.7;
}

.experience-description .description {
  text-align: justify;
}

#links{
  background-color: rgba(33, 41, 49, 0.3);
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}

.links{
  display: flex;
  flex-direction: column;
}

.links>*{
  font-size: 0.8rem;
  line-height: 1.5rem;
  padding: 4rem;
  vertical-align: top;
}
.links h3{
  font-size: 0.8rem;
  width: 8em;
  text-transform: uppercase;
  margin: 0;
}
.links p{
  margin: 0;
}
.links a{
  text-decoration: none;
  color: rgb(33, 41, 49);
}
.links i{
  font-size: 1.5rem;
  transition: opacity ease 0.5s;
}
.links a:hover{
  color: rgba(33, 41, 49, 0.6);
}

/* .links *:first-child{
  border-bottom: 2px solid rgba(33, 41, 49, 0.4);
} */

.links h3:first-child, .links > p:first-child, .links > a:first-child:not(p a) {
  border-bottom: 2px solid rgba(33, 41, 49, 0.4);
}

.links.last{
  flex-grow: 1;
}

.links.last a {
  font-size: 1.25rem;
}

/*Responsive Styles*/
@media screen and (max-width: 1300px) {
  #contentContainer{
    width: 92%;
  }
  html{
    font-size: 14px;
  }
}
@media screen and (max-width: 880px) {
  html{
    font-size: 12px;
  }
  .experience {
    flex-direction: column;
    align-items: center;
    border-bottom: solid 2px rgba(33, 41, 49, 0.3);
  }

  .experience.last {
    border-bottom: 0px;
  }
  
  .experience:nth-child(odd){
    flex-direction: column;
    align-items: center;
  }

  .experience-image-container{
    width: 350px;
  }

  .experience-description{
    width: 350px;
    margin-top: 2.5rem;
  }

  .links.first{
    display: none;
  }
}
@media screen and (max-width: 450px) {
  html{
    font-size: 9px;
  }
  .experience-image-container{
    width: 100%;
  }

  .experience-description{
    width: 100%;
    margin-top: 2.5rem;
  }
}
@media screen and (max-width: 350px) {
  html{
    font-size: 8px;
  }
}
@media screen and (min-width: 2000px){
  html{
    font-size: 20px;
  }
  #contentContainer{
    width: 1800px;
  }
}